/***** Google fonts import ************/
@import url("https://fonts.googleapis.com/css?family=Montserrat:100,300,400,500|Poppins:200,400,500,600,700");
@import "general/base/variables";
@import "general/utils/mixins";
@import "general/utils/functions";
@import "general/base/reset";
@import "general/base/preloader";
@import "home/layout/footer";
@import "home/layout/banner";

.demo-item{
    margin-bottom: 50px;
    .demo-screenshot{
        margin-bottom: 20px;
        box-shadow: 0 0 60px 10px rgba(220, 220, 220, 0.5);
        img{
            width: 100%;
        }
    }
    h6{
        text-transform: capitalize;
        text-align: center;
        color: $nav-color;
    }
    a{
        &:hover,&:focus{
            h6{
                color: $theme-color-style-2;
            }
        }
    }
}

/**
*
* More Demo's
*
*/
.more-demo-item{
    width: 100%;
    display: flex;
    align-items: center;
    height: 400px;
    @include gradient($gradient-overlay-style-2);
    justify-content: center;
    text-align: center;
    h3{
        color: $white;
        span{
            width: 100%;
            display: block;
            font-size: 70%;
        }
    }
}

.home-features{
    box-shadow: 0 0 60px 10px rgba(220,220,220,.3);
    .feature-box {
        display: flex;
        padding: 40px;
        @include border(left, 1px, solid, $border-color);
        @include border(bottom, 1px, solid, $border-color);
        flex-direction: column;
        text-align: center;
        @media (min-width:991px){
            &:nth-child(3n+1) {
                border-left: none;
            }
        }
    }
    .feature-description{
        margin-bottom: 0;
    }
    .feature-icon {
        color: $theme-color-style-2;
        -webkit-text-fill-color: transparent;
        -webkit-background-clip: text;
        @include gradient($gradient-overlay-style-2);
        font-size: 36px;
        margin-right: 25px;
        [class*=" flaticon-"], [class^=flaticon-]{
            &:after,&:before{
                font-size: 40px;
                margin-left: 0;
            }
        }
    }
}