.home-banner {
    padding: 150px 0;
    position: relative;
    background: url("#{$image-path}/hero-img.jpg") no-repeat center center / cover;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    &:after{
        @include position(absolute,0,0,0,0);
        @include gradient($gradient-overlay-style-2);
        opacity: 0.9;
        content: '';
    }
    /**
    * ----------------------------------------------
    * Banner content  css
    * ----------------------------------------------
    */
    .landing-banner-content{
        position: relative;
        z-index: 2;
        @media (max-width: 991px) {
            color: $white;
        }
        h1{
          margin: 3rem 0 5rem 0;
          color: $white;
          font-weight: 300;
          font-size: 2rem;
          span{
            font-weight: 500;
          }
        }
    }

    /*
    *
    * Store button reset
    *
    */
    .store-btn{
        a{
            background-color: $white;
            color: $theme-color-style-2;
            @include border(null, 1px, solid, $white);
            border-radius: 5px;
            margin-right: 1rem;
            @include transition(all 0.5s);
            padding: 1.5rem 2rem;
            &:last-child{
              margin-right: 0;
              background:none;
              color: $white;
              &:hover{
                background-color: $white;
                color: $theme-color-style-2;
              }
            }
            &:hover{
              color: $white;
              background:none;
            }
        }
    }
  }
  