/**
 * ----------------------------------------------
 * Font face
 * 
 */

$body-font: 'Roboto',sans-serif;
$heading-font: 'Poppins', sans-serif;
$fontawesome-font-family: "FontAwesome";

/**
 * ----------------------------------------------
 * Font Sizes
 * ----------------------------------------------
 *
*/
$default-font-size: 16px;
$grid-title-font-size: 14px;
$author-size: 12px;
$service-icon-font-size: 2.75rem;


/**
 * ----------------------------------------------
 * COLOR CODES
 * ----------------------------------------------
 *
*/

$white:                 #ffffff;
$black:                 #000000;
$default-color:         #777;
$heading-color:         #343A3A;
$subtitle-color:        #888888;
$border-color:          #f1f2f3;
$bg-light:              #fcfcfc !important;
$light:                 #fcfcfc;
$nav-color:             rgba(0,0,0,1);
$para-color:            #464646;
$tab-title-color:       rgba(0,0,0,0.5);
$overlay-bg:            rgba(0,0,0,1);
$rating-color:          #fac50f;
$image-path:            '../../images';
$footer-bg:             #444444;
$copyright-bg:          #2E303C;
$link-color:            #222;
$dot-color:             #ccc;
$sidemenu-bg:           #0c0c0c; 

/**
* ----------------------------------------------
* Shadows CODES
* ----------------------------------------------
*
*/
$box-shadow: 0 0 60px 10px rgba(220,220,220,.3);



/**
 * ----------------------------------------------
 * Loop classes
 * ----------------------------------------------
 *
*/
$social-colors: (fb:#3b579d, tw:#1da1f2, dribble: #ea4c89, ln:#0177b4, gplus:#dc4a38);
$social-hvr-colors: (fb:#2d4278, tw:#0c85d0, dribble: #e51e6b, ln:#015581, gplus:#bf3322);

$theme-color-style-1:  #3584A7;
$theme-color-style-2:  #4458dc;
$heading-color-style-2:         #2540f0;
$subtitle-color-style-2:        #3c54f2;
$theme-color-style-3:           #3264f5;
$secondary-color-style-3:       #7202bb;

/**
* ----------------------------------------------
* Grediant CODES
* ----------------------------------------------
*
*/
$btn-gred-style-1: linear-gradient(to right, #473B7B 0%, #3584A7 51%, #473B7B 100%);
$btn-outline-gred-style-1: linear-gradient(to right, #473B7B 0%, #3584A7 100%);
$gradient-overlay-style-1: linear-gradient(-225deg, #473B7B 0%, #3584A7 51%, #30D2BE 100%);
$text-gradient-style-1: linear-gradient(to right, #3584A7 0%, #473B7B 100%);
$text-gradient-reverse-style-1: linear-gradient(-225deg, #30D2BE 0%,#3584A7 50%, #473B7B 100%);

$btn-gred-style-2: linear-gradient(to right, #4458dc 0%, #800fee 51%, #2540f0 100%);
$btn-outline-gred-style-2: linear-gradient(white, white), radial-gradient(circle at top left, #4458dc, #800fee);
$gradient-overlay-style-2: linear-gradient(to right, #4458dc 2%, #800fee 80%);
$text-gradient-style-2: linear-gradient(to right, #2540f0 2%, #800fee 90%);

$btn-gred-style-3: linear-gradient(to right, #7202bb 0%, #3264f5 51%, #7202bb 100%);
$btn-outline-gred-style-3: linear-gradient(white, white), radial-gradient(circle at top left, #7202bb, #3264f5);
$gradient-overlay-style-3: linear-gradient(to right, #7202bb 2%, #3264f5 82%);
$text-gradient-style-3: linear-gradient(to right, #7202bb 2%, #3264f5 82%);
$text-gradient-reverse-style-3: linear-gradient(to right, #3264f5 2%, #7202bb 82%);


